@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios {
    display: grid;
    gap: 2rem;
    @include m.tablet {
        @include m.grid(2, 2rem);
    }

    .servicio {
        text-align: center;
        background-color: v.$blanco;
        padding: 1rem;
        border-radius: .8rem;
        transition-property: transform;
        transition: 300ms;

        &.seleccionado {
            background-color: v.$azul;
            
            p {
                color: v.$blanco;
            }
        }
        &:hover{
            transform: scale(1.1);
            cursor: pointer;
            background-color: v.$grisM;
        }

        p{
            color: v.$negro;
            margin: 0 0 calc(v.$separacion /4) 0;
        }
        .precio-servicio{
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }
    }
}